*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: "Lora", serif;
    font-size: 12px;
    color: #6C757D;

    background-color: #fff;

}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
}

p {
    margin: 0;
}

/* Container
=================== */
.about__container {
    width: 100%;
    padding: 0 70px;
}

/* Lines 
============*/
.about__line__1 {
    width: 20px;
    height: 0px;
    left: 1127px;
    top: 30px;
    margin: 5px 17px 0 17px;

    border: 0.5px solid #fff;
    transform: rotate(90deg);
}

.about__line__2 {
    width: 20px;
    height: 0px;
    left: 1333px;
    top: 30px;
    margin: 5px 17px 0 17px;

    border: 0.5px solid #fff;
    transform: rotate(90deg);
}


/* Header
==================== */
.about__header {
    width: 100%;
    height: 80px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1000;

    background: rgba(0, 0, 0, 0.2);
}

.about__header__inner {
    display: flex;
    justify-content: space-between;
    padding: 29px 0;
    align-items: center;
}

.about__header__name {
    font-family: 'League Spartan', sans-serif;
    font-weight: 600;
    font-size: 20px;

    color: #fff;

    text-transform: uppercase;
}

/* Nav 
===================== */
.about__nav {
    display: flex;
}

.about__nav__blogs {
    font-size: 12px;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
}

.about__nav__link {
    text-decoration: none;
    margin-left: 25px;

    color: #fff;
}

.about__nav__link:hover {
    color: #FBC252;
}

/* Social
================= */
.about__social {
    display: flex;
    margin-top: -3px;
}

.about__social__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;

    margin-right: 18px;

    transition: background-color .1s linear;
}

.about__social__item:last-child {
    margin-right: 0;
}

.about__social__item:hover .about__social__icon {
    fill: #FBC252;
}

.about__social__icon {
    display: block;

    fill: #fff;

    transition: fill .1s linear;

}

/* Search 
====================*/
.about__search__div {
    display: flex;
    margin-top: -3px;

}

.about__search__div:hover .about__search__icon {
    fill: #FBC252;
    cursor: pointer;
}

.about__search__icon {
    display: block;
    height: 18px;
    width: 18px;

    fill: #fff;

    transition: fill .1s linear;
}



/* Intro
================ */
.about__intro {
    display: flex;
    flex-direction: column;
    height: 620px;

    background: #343A40 url("../../img/about-bg.jpg") center no-repeat;
    background-size: cover;
}

.about__intro__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-top: 100px;
}

.about__intro__name {
    width: 320px;
    height: 52.82%;
    font-family: "Lora", serif;
    font-size: 26px;
    font-weight: 700;
    text-align: center;

    color: #fff;
}

.about__intro__title {
    margin-top: 70px;
    font-family: "Lora", serif;
    font-size: 60px;
    font-weight: 400;
    width: 350px;
    text-align: center;

    color: #fff;
}

.about__intro__subtitle {
    margin-top: 20px;
    margin-bottom: 20px;
    font-family: "Lora", serif;
    font-size: 14px;
    font-weight: 400;
    width: 420px;
    line-height: 25px;
    text-align: center;

    color: #fff;
}

.about__intro__line1 {
    width: 200px;
    height: 0px;

    margin-top: 23px;

    border: 1px solid #E5E5E5;
}

.about__intro__line2 {
    width: 70px;
    height: 0px;

    border: 1px solid #E5E5E5;
}


/* About */

.about__about {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    padding: 0 170px;

    margin-top: 60px;
    margin-bottom: 100px;
}

.about__about__item {
    display: flex;
    flex-direction: row;
    width: 50%;

    margin-top: 40px;
    padding: 0 20px;
}

.about__item__icon {
    margin-right: 16px;
}

.about__item__photo {
    width: 30px;
    height: 30px;
}

.about__item__title {
    font-family: "Lora", serif;
    font-size: 20px;
    font-weight: 700;
    margin: 2.2px 0;

    color: #000;
}

.about__item__subtitle {
    font-family: "Lora", serif;
    font-size: 14px;
    font-weight: 400;
    margin-top: 16px;
}

/* Footer
================== */
.about__footer {
    display: flex;
    
    height: 400px;

    background: #212529;
}

.about__footer__content {
    display: flex;
    margin-top: 106px;
}

.about__content__contact {
    display: flex;
    flex-direction: column;
}

.about__contact__title {
    font-family: "Lora", serif;
    font-size: 18px;
    font-weight: 700;

    color: #fff;
}

.about__footer__a {
    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;
    margin-top: 18px;

    text-decoration: none;

    color: #E5E5E5;
}

.about__footer__a1 {
    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;
    margin-top: 16px;

    text-decoration: none;

    color: #E5E5E5;
}

.about__content__explorate {
    display: flex;
    flex-direction: column;
    margin-left: 142px;
}

.about__explorate__title {
    font-family: "Lora", serif;
    font-size: 18px;
    font-weight: 700;

    color: #fff;
}

.about__content__headquarter {
    display: flex;
    flex-direction: column;
    margin-left: 229px;
}

.about__head__title {
    font-family: "Lora", serif;
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 11px;

    color: #fff;
}

.about__content__headquarter p {
    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;
    margin-top: 5px;

    color: #E5E5E5;
}

.about__content__connections {
    display: flex;
    flex-direction: column;
    margin-left: 229px;
}

.about__connection__title {
    font-family: "Lora", serif;
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 11px;

    color: #fff;
}

.about__connection__social {
    display: flex;
}


/* Sub-footer
======================= */
.about__sub__footer {
    display: flex;
    align-items: center;

    height: 80px;

    background: #343A40;
}

.about__subfooter__content {
    display: flex;
    justify-content: space-between;
}

.about__subfooter__studio {
    font-family: "Lora", serif;
    font-size: 14px;
    font-weight: 700;

    color: #fff;
}

.about__subfooter__subscribe {
    font-family: "Lora", serif;
    font-size: 14px;
    font-weight: 400;

    color: #fff;
}

/* Burger */
.about__burger {
    padding: 9px 2px;

    display: none;
    background: none;
    border: 0;
    cursor: pointer;
}

.about__burger__item {
    display: block;
    width: 30px;
    height: 3px;

    background-color: #fff;

    font-size: 0;
    color: transparent;

    position: relative;
}

.about__burger__item:before,
.about__burger__item:after {
    content: "";
    width: 100%;
    height: 100%;

    background-color: #fff;

    position: absolute;
    left: 0;
    z-index: 1;
}

.about__burger__item:before {
    top: -8px;
}

.about__burger__item:after {
    bottom: -8px;
}



/* Media */

@media (max-width: 1140px) {
    /* Footer */
    .about__content__explorate {
        margin-left: 100px;
    }
    
    .about__content__headquarter,
    .about__content__connections {
        margin-left: 170px;
    } 
}

@media (max-width: 1060px) {

    /* Intro */

    .about__intro {
        height: auto;
    }

    .about__intro__inner {
        margin-bottom: 100px;
    }

    .about__intro__name {
        font-size: 20px;
    }

    .about__intro__title {
        font-size: 40px;
    }

    .about__intro__line1 {
        width: 120px;
    }

    .about__intro__subtitle {
        font-size: 12px;
    }

    .about__intro__line2 {
        width: 40px;
    }

    /* About */

    .about__about {
        padding: 0 100px;
    }

    .about__about__item {
        width: 100%;
    }

    /* Footer */
    .about__contact__title,
    .about__explorate__title,
    .about__head__title,
    .about__connection__title {
        font-size: 12px;
    }
    
    .about__content__explorate {
        margin-left: 80px;
    }
    
    .about__content__headquarter,
    .about__content__connections {
        margin-left: 150px;
        }    
}

@media (max-width: 821px) {

    .about__container {
        padding: 0 20px;
    }

    /* About */
    .about__about {
        padding: 0 50px;
    }

        /* Footer */
        .about__footer__content {
            margin-top: 50px;
            flex-wrap: wrap;
        }
    
        .about__content__contact,
        .about__content__explorate {
            width: 46%;
        }
    
        .about__content__contact,
        .about__content__explorate {
            margin-bottom: 15px;
        }
    
        .about__content__headquarter,
        .about__content__connections {
            width: 40%;
        }
    
        .about__contact__title,
        .about__explorate__title,
        .about__head__title,
        .about__connection__title {
            font-size: 12px;
        }
    
        .about__content__explorate {
            margin-left: 10px;
        }
    
        .about__content__contact,
        .about__content__headquarter {
            margin-left: 20px;
        }

        .about__content__connections {
            margin-left: 50px;
        }
    
        /* Sub footer */
    
        .about__subfooter__content {
            flex-direction: column;
            justify-content: center;
            align-items: center;
        }
    
        .about__subfooter__studio {
            margin-bottom: 10px;
        }    
}

