*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: "Lora", serif;
    font-size: 12px;
    color: #6C757D;

    background-color: #fff;

}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
}

p {
    margin: 0;
}

/* Container
=================== */
.art__container {
    width: 100%;
    padding: 0 70px;
}

/* Lines 
============*/
.art__line__1 {
    width: 20px;
    height: 0px;
    left: 1127px;
    top: 30px;
    margin: 5px 17px 0 17px;

    border: 0.5px solid #fff;
    transform: rotate(90deg);
}

.art__line__2 {
    width: 20px;
    height: 0px;
    left: 1333px;
    top: 30px;
    margin: 5px 17px 0 17px;

    border: 0.5px solid #fff;
    transform: rotate(90deg);
}


/* Header
==================== */
.art__header {
    width: 100%;
    height: 80px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1000;

    background: #434242;
}

.art__header__inner {
    display: flex;
    justify-content: space-between;
    padding: 29px 0;
    align-items: center;
}

.art__header__name {
    font-family: 'League Spartan', sans-serif;
    font-weight: 600;
    font-size: 20px;

    color: #fff;

    text-transform: uppercase;
}

/* Nav 
===================== */
.art__nav {
    display: flex;
}

.art__nav__blogs {
    font-size: 12px;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
}

.art__nav__link {
    text-decoration: none;
    margin-left: 25px;

    color: #fff;
}

.art__nav__link:hover {
    color: #D6E4E5;
}

/* Social
================= */
.art__social {
    display: flex;
    margin-top: -3px;
}

.art__social__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;

    margin-right: 18px;

    transition: background-color .1s linear;
}

.art__social__item:last-child {
    margin-right: 0;
}

.art__social__item:hover .social__icon {
    fill: #D6E4E5;
}

.art__social__icon {
    display: block;

    fill: #fff;

    transition: fill .1s linear;

}

/* Search 
====================*/
.art__search__div {
    display: flex;
    margin-top: -3px;

}

.art__search__div:hover .search__icon {
    fill: #D6E4E5;
    cursor: pointer;
}

.art__search__icon {
    display: block;
    height: 18px;
    width: 18px;

    fill: #fff;

    transition: fill .1s linear;
}


/* Intro */
.art__intro {
    display: flex;
    flex-direction: column;
    height: 590px;

    background-color: #FEFCF3;
}

.art__intro__inner {
    display: flex;
    flex-direction: column;
    margin-top: 110px;
}

.art__intro__title {
    font-family: 'League Spartan', sans-serif;
    font-weight: 600;
    font-size: 70px;
    margin-left: 190px;

    color: #000;
}

.art__intro__item {
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 20px;
}

.art__intro__photo {
    width: 50%;
    margin-left: 90px;
}

.art__item__photo {
    width: 520px;
    height: 320px;

    object-fit: cover;

    border-radius: 10px;
}

.art__item__text {
    display: flex;
    flex-direction: column;
    width: 50%;
    padding: 0 150px;

    margin-top: 20px;
    margin-left: -250px;
}

.art__item__date {
    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400px;

    color: #000;
}

.art__item__title {
    font-family: "Lora", serif;
    font-size: 28px;
    font-weight: 700;
    
    margin-top: 15px;

    color: #000;
}

.art__item__subtitle {
    font-family: "Lora", serif;
    font-size: 14px;
    font-weight: 400;

    margin-top: 20px;

    color: #000;
}

/* Topics
======================= */

.art__topics__bg {
    background: #FEFCF3;
}

.art__topics {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.art__topics__post {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    margin-bottom: 100px;
}

.art__topics__row {
    display: flex;
    margin-bottom: 20px;
}

.art__post__item {
    width: 25%;
    padding: 0 10px;
    overflow: hidden;

    margin-bottom: 20px;
}

.art__post__item5 {
    width: 25%;
    padding: 0 8px;
    overflow: hidden;

    margin-bottom: 20px;
}

.art__post__item:first-child {
    margin-left: 0;
}

.art__post__photo {
    position: relative;
    width: 100%;
    height: 280px;
    margin-bottom: 15px;

    object-fit: cover;

    border-radius: 5px;
}

.art__post__filter {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;

    margin-top: 20px;
    margin-left: 233px;
    padding: 6px 10px;

    background: rgba(255, 255, 255, 0.15);
    border-radius: 8px;

    z-index: 100;
}

.art__post__filter2 {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;

    margin-top: 20px;
    margin-left: 253px;
    padding: 6px 10px;

    background: rgba(255, 255, 255, 0.15);
    border-radius: 8px;

    z-index: 100;
}

.art__post__filter3 {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;

    margin-top: 20px;
    margin-left: 248px;
    padding: 6px 10px;

    background: rgba(255, 255, 255, 0.15);
    border-radius: 8px;

    z-index: 100;
}

.art__post__category {
    font-family: "Roboto", sans-serif;
    font-size: 10px;
    font-weight: 700;

    text-transform: uppercase;

    color: #fff;
}

.art__post__date {
    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;

    margin-bottom: 15px;

    color: #000;
}

.art__post__title {
    width: 273px;
    height: 50px;
    margin-bottom: 15px;

    font-family: "Lora", serif;
    font-size: 18px;
    font-weight: 700;

    color: #000;
}

.art__post__subtitle {
    width: 310px;
    height: 60px;

    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;

    color: #000;
}

/* Footer
================== */
.art__footer {
    display: flex;
    
    height: 400px;

    background: #212529;
}

.art__footer__content {
    display: flex;
    margin-top: 106px;
}

.art__content__contact {
    display: flex;
    flex-direction: column;
}

.art__contact__title {
    font-family: "Lora", serif;
    font-size: 18px;
    font-weight: 700;

    color: #fff;
}

.art__footer__a {
    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;
    margin-top: 18px;

    text-decoration: none;

    color: #E5E5E5;
}

.art__footer__a1 {
    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;
    margin-top: 16px;

    text-decoration: none;

    color: #E5E5E5;
}

.art__content__explorate {
    display: flex;
    flex-direction: column;
    margin-left: 142px;
}

.art__explorate__title {
    font-family: "Lora", serif;
    font-size: 18px;
    font-weight: 700;

    color: #fff;
}

.art__content__headquarter {
    display: flex;
    flex-direction: column;
    margin-left: 229px;
}

.art__head__title {
    font-family: "Lora", serif;
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 11px;

    color: #fff;
}

.art__content__headquarter p {
    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;
    margin-top: 5px;

    color: #E5E5E5;
}

.art__content__connections {
    display: flex;
    flex-direction: column;
    margin-left: 229px;
}

.art__connection__title {
    font-family: "Lora", serif;
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 11px;

    color: #fff;
}

.art__connection__social {
    display: flex;
}


/* Sub-footer
======================= */
.art__sub__footer {
    display: flex;
    align-items: center;

    height: 80px;

    background: #343A40;
}

.art__subfooter__content {
    display: flex;
    justify-content: space-between;
}

.art__subfooter__studio {
    font-family: "Lora", serif;
    font-size: 14px;
    font-weight: 700;

    color: #fff;
}

.art__subfooter__subscribe {
    font-family: "Lora", serif;
    font-size: 14px;
    font-weight: 400;

    color: #fff;
}

/* Burger */
.art__burger {
    padding: 9px 2px;

    display: none;
    background: none;
    border: 0;
    cursor: pointer;
}

.art__burger__item {
    display: block;
    width: 30px;
    height: 3px;

    background-color: #fff;

    font-size: 0;
    color: transparent;

    position: relative;
}

.art__burger__item:before,
.art__burger__item:after {
    content: "";
    width: 100%;
    height: 100%;

    background-color: #fff;

    position: absolute;
    left: 0;
    z-index: 1;
}

.art__burger__item:before {
    top: -8px;
}

.art__burger__item:after {
    bottom: -8px;
}



