*,
*::before,
*::after {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: "Lora", serif;
    font-size: 12px;
    color: #6C757D;

    background-color: #fff;

}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
}

p {
    margin: 0;
}

/* Container
=================== */
.container {
    width: 100%;
    padding: 0 70px;
}

/* Lines 
============*/
.line__1 {
    width: 20px;
    height: 0px;
    left: 1127px;
    top: 30px;
    margin: 5px 17px 0 17px;

    border: 0.5px solid #fff;
    transform: rotate(90deg);
}

.line__2 {
    width: 20px;
    height: 0px;
    left: 1333px;
    top: 30px;
    margin: 5px 17px 0 17px;

    border: 0.5px solid #fff;
    transform: rotate(90deg);
}


/* Header
==================== */
.header {
    width: 100%;
    height: 80px;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1000;

    background: rgba(0, 0, 0, 0.2);
}

.header__inner {
    display: flex;
    justify-content: space-between;
    padding: 29px 0;
    align-items: center;
}

.header__name {
    font-family: 'League Spartan', sans-serif;
    font-weight: 600;
    font-size: 20px;

    color: #fff;

    text-transform: uppercase;
}

/* Nav 
===================== */
.nav {
    display: flex;
}

.nav__blogs {
    font-size: 12px;
    font-family: 'Roboto', sans-serif;
    font-weight: 700;
}

.nav__link {
    text-decoration: none;
    margin-left: 25px;

    color: #fff;
}

.nav__link:hover {
    color: #3C6255;
}

/* Social
================= */
.social {
    display: flex;
    margin-top: -3px;
}

.social__item {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20px;
    height: 20px;

    margin-right: 18px;

    transition: background-color .1s linear;
}

.social__item:last-child {
    margin-right: 0;
}

.social__item:hover .social__icon {
    fill: #3C6255;
}

.social__icon {
    display: block;

    fill: #fff;

    transition: fill .1s linear;

}

/* Search 
====================*/
.search__div {
    display: flex;
    margin-top: -3px;

}

.search__div:hover .search__icon {
    fill: #3C6255;
    cursor: pointer;
}

.search__icon {
    display: block;
    height: 18px;
    width: 18px;

    fill: #fff;

    transition: fill .1s linear;
}

/* Intro
================ */
.intro {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 620px;

    background: #343A40 url("../../img/intro-bg.jpg") center no-repeat;
    background-size: cover;
}

.intro__inner {
    width: 100%;
    margin-top: 138px;
}

.intro__category {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
    padding: 5px 10px;
    height: 22px;
    width: 77px;

    background: rgba(255, 255, 255, 0.15);
    border-radius: 8px;
}

.category__name {
    font-family: "Roboto", sans-serif;
    font-size: 10px;
    font-weight: 700;

    color: #fff;
}

.intro__title {
    width: 530px;
    height: 52.82%;
    font-family: "Lora", serif;
    font-size: 36px;
    font-weight: 700;

    color: #fff;
}

.intro__subtitle {
    display: flex;
}

.subtitle__date {
    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;

    margin-top: 15px;
    margin-right: 11px;

    color: #fff;
}

.intro__line3 {
    width: 30px;
    height: 0px;

    margin-top: 23px;
    margin-right: 10px;

    border: 1px solid #E5E5E5;
}

.subtitle__text {
    margin-top: 13px;
    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;
    width: 420px;

    color: #fff;
}


/* Topics
======================= */
.topics {
    display: flex;
    flex-direction: column;
    margin-top: 150px;
}

.topics__title {
    font-family: "Lora", serif;
    font-size: 36px;
    font-weight: 700;

    margin-bottom: 30px;

    color: #495057;
}

.topics__filter {
    display: flex;
    justify-content: space-between;

    margin-bottom: 30px;
}

.filter__name {
    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 700;

    margin-right: 20px;

    text-decoration: none;

    color: #495057;

    transition: color .1s;
}

.filter__name:hover {
    color: #D4A373;
}

.topics__post {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    margin-bottom: 100px;
}

.topics__row {
    display: flex;
    margin-bottom: 50px;
}

.post__item {
    width: 25%;
    padding: 0 10px;
    overflow: hidden;

    margin-bottom: 50px;
}

.post__item5 {
    width: 25%;
    padding: 0 8px;
    overflow: hidden;

    margin-bottom: 50px;
}

.post__item:first-child {
    margin-left: 0;
}

.post__photo {
    position: relative;
    width: 100%;
    height: 280px;
    margin-bottom: 15px;

    object-fit: cover;

    border-radius: 5px;
}

.post__filter {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;

    margin-top: 20px;
    margin-left: 233px;
    padding: 6px 10px;

    background: rgba(255, 255, 255, 0.15);
    border-radius: 8px;

    z-index: 100;
}

.post__filter2 {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;

    margin-top: 20px;
    margin-left: 253px;
    padding: 6px 10px;

    background: rgba(255, 255, 255, 0.15);
    border-radius: 8px;

    z-index: 100;
}

.post__filter3 {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;

    margin-top: 20px;
    margin-left: 248px;
    padding: 6px 10px;

    background: rgba(255, 255, 255, 0.15);
    border-radius: 8px;

    z-index: 100;
}

.post__category {
    font-family: "Roboto", sans-serif;
    font-size: 10px;
    font-weight: 700;

    text-transform: uppercase;

    color: #fff;
}

.post__date {
    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;

    margin-bottom: 15px;
}

.post__title {
    width: 273px;
    height: 50px;
    margin-bottom: 15px;

    font-family: "Lora", serif;
    font-size: 18px;
    font-weight: 700;
}

.post__subtitle {
    width: 310px;
    height: 60px;

    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;
}

/* Big Post
====================== */
.big__post {
    display: flex;
    justify-content: center;
    height: 600px;

    background: url("../../img/big-post.jpg") center no-repeat;
    background-size: cover;

    opacity: .9;
}

.big__span {
    width: 100%;
    display: flex;
    justify-content: center;

    background-color: rgba(0, 0, 0, 0.6);
}


.big__contents {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.big__filter {
    display: flex;

    padding: 6px 10px;

    background: rgba(255, 255, 255, 0.15);
    border-radius: 8px;
}

.big__category {
    font-family: "Roboto", sans-serif;
    font-size: 10px;
    font-weight: 700;

    text-transform: uppercase;

    color: #fff;
}

.big__title {
    width: 530px;
    height: 92px;
    font-family: "Lora", serif;
    font-size: 36px;
    font-weight: 700;
    margin-bottom: 13px;

    text-align: center;

    color: #fff;
}

.big__subtitle {
    width: 420px;

    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;
    margin-bottom: 24px;

    text-align: center;
    line-height: 20px;

    color: #fff;
}

.big__line {
    width: 30px;
    height: 0px;
    margin-bottom: 15px;

    border: 1px solid #E5E5E5;
}

.big__date {
    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;

    color: #fff;
}


/* Blog Metro
=========================== */
.metro__inner {
    display: flex;
    flex-direction: column;
    margin-top: 149px;
    margin-bottom: 149px;
}

.metro__title {
    font-family: 'Lora', serif;
    font-weight: 700;
    font-size: 36px;
    margin-bottom: 70px;

    color: #495057;
}

.metro__blogs {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.blog__item1 {
