}

/* Footer
================== */
.footer {
    display: flex;
    
    height: 400px;

    background: #212529;
}

.footer__content {
    display: flex;
    margin-top: 106px;
}

.content__contact {
    display: flex;
    flex-direction: column;
}

.contact__title {
    font-family: "Lora", serif;
    font-size: 18px;
    font-weight: 700;

    color: #fff;
}

.footer__a {
    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;
    margin-top: 18px;

    text-decoration: none;

    color: #E5E5E5;
}

.footer__a1 {
    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;
    margin-top: 16px;

    text-decoration: none;

    color: #E5E5E5;
}

.content__explorate {
    display: flex;
    flex-direction: column;
    margin-left: 142px;
}

.explorate__title {
    font-family: "Lora", serif;
    font-size: 18px;
    font-weight: 700;

    color: #fff;
}

.content__headquarter {
    display: flex;
    flex-direction: column;
    margin-left: 229px;
}

.head__title {
    font-family: "Lora", serif;
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 11px;

    color: #fff;
}

.content__headquarter p {
    font-family: "Lora", serif;
    font-size: 12px;
    font-weight: 400;
    margin-top: 5px;

    color: #E5E5E5;
}

.content__connections {
    display: flex;
    flex-direction: column;
    margin-left: 229px;
}

.connection__title {
    font-family: "Lora", serif;
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 11px;

    color: #fff;
}

.connection__social {
    display: flex;
}


/* Sub-footer
======================= */
.sub__footer {
    display: flex;
    align-items: center;

    height: 80px;

    background: #343A40;
}

.subfooter__content {
    display: flex;
    justify-content: space-between;
}

.subfooter__studio {
    font-family: "Lora", serif;
    font-size: 14px;
    font-weight: 700;

    color: #fff;
}

.subfooter__subscribe {
    font-family: "Lora", serif;
    font-size: 14px;
    font-weight: 400;

    color: #fff;
}

/* Burger */
.burger {
    padding: 9px 2px;

    display: none;
    background: none;
    border: 0;
    cursor: pointer;
}

.burger__item {
    display: block;
    width: 30px;
    height: 3px;

    background-color: #fff;

    font-size: 0;
    color: transparent;

    position: relative;
}

.burger__item:before,
.burger__item:after {
    content: "";
    width: 100%;
    height: 100%;

    background-color: #fff;

    position: absolute;
    left: 0;
    z-index: 1;
}

.burger__item:before {
    top: -8px;
}

.burger__item:after {
    bottom: -8px;
}



